import React, { Fragment, useEffect, useState, useRef } from 'react'
import {
    ChevronDown, Share, Printer, FileText, File, Grid, Copy, Plus, MoreVertical, Edit, Archive, Trash, Search, Eye, Lock, ChevronLeft, RefreshCw
} from 'react-feather'
import {
    Card, CardHeader, CardTitle, Button, UncontrolledButtonDropdown, DropdownToggle, DropdownMenu, DropdownItem, Input, Label, Row, Col, Badge, Form, FormGroup, UncontrolledDropdown, Spinner, CardBody, TabContent, TabPane, Nav, NavItem, NavLink, InputGroup, InputGroupAddon, InputGroupText, CustomInput
} from 'reactstrap'
import Select from 'react-select'
import { selectThemeColors, transformInToFormObject } from '@utils'
import useJwt from '@src/auth/jwt/useJwt'
import { toast } from 'react-toastify'
import { useHistory } from 'react-router-dom'
import { PHONE_PREFIX } from '../../../Configuarables'
import { Error, Success, ErrorMessage } from '../../../viewhelper'
import Swal from 'sweetalert2'
import withReactContent from 'sweetalert2-react-content'
import ImgUpload from '../../ImgUpload'
const MySwal = withReactContent(Swal)

const EditContractor = () => {
    const history = useHistory()
    const branch_idRef = useRef()
    const userRole = JSON.parse(localStorage.getItem('userData')).role
    const contractorTypeRef = useRef()
    const addressRef = useRef()
    const EditContractorInfo = JSON.parse(localStorage.getItem('EditContractorInfo'))
    const [imgUpLoading, setimgUpLoading] = useState(false)
    const [fileUrls, setFileUrls] = useState(EditContractorInfo.photo ? [
        {
            uid: 1,
            name: 'image.png',
            status: 'done',
            type: "image/jpeg",
            url: EditContractorInfo.photo
        }
    ] : [])
    const [state, setState] = useState({
        previewVisible: false,
        previewImage: '',
        previewTitle: '',
        fileList: EditContractorInfo.photo ? [
            {
                uid: 1,
                name: 'image.png',
                status: 'done',
                type: "image/jpeg",
                url: EditContractorInfo.photo
            }
        ] : []
    })
    const [isLoading, setLoading] = useState(false)
    const [postcode, setPostCode] = useState('')
    const [searching, setSearching] = useState(false)
    const [ukaddress, setukaddress] = useState([])
    const [userInput, setUserInput] = useState({
        sub_estate_manager_id: EditContractorInfo.user_id,
        contractor_type_id: EditContractorInfo.contractor_link.contractor_type.contractor_type_id,
        contractor_type_name: EditContractorInfo.contractor_link.contractor_type.type_name,
        mobile: EditContractorInfo.mobile,
        email: EditContractorInfo.email,
        full_name: EditContractorInfo.full_name,
        address: EditContractorInfo.address
    })
    const [BranchList, setBranchList] = useState([])
    const [contractorTypeList, setcontractorTypeList] = useState([])
    useEffect(() => {
        useJwt.BranchList().then(res => {
            // console.log(res.data.payload)
            setBranchList(res.data.payload)
        }).catch(err => {
            Error(err)
            console.log(err)
        })
        useJwt.ContractorTypeList().then(res => {
            console.log(res.data.payload)
            setcontractorTypeList(res.data.payload)
        }).catch(err => {
            Error(err)
            console.log(err)
        })
    }, [])
    const handlePostCodeSearch = e => {
        e.preventDefault()
        setSearching(true)
        useJwt.ukAddressEndpoint({ postCode: postcode }).then(res => {
            console.log(res.data.payload)
            // const { latitude, longitude } = res.data.payload
            // setUserInput({ ...userInput, latitude, longitude })
            setukaddress(res.data.payload.addresses.map(element => `${element.building_number}, ${element.thoroughfare}, ${element.locality}, ${element.town_or_city}`))
            setSearching(false)
        }).catch(err => {
            setukaddress([])
            setSearching(false)
            Error(err)
        })
    }
    const [error, seterror] = useState({
        email: false,
        mobile: false
    })
    const handleChange = e => {
        const chkEmail = /\S+@\S+\.\S+/.test(e.target.value)
        // Email Validator
        if (e.target.name === 'email' && chkEmail) {
            seterror({ ...error, email: false })
        }
        if (e.target.name === 'email' && !chkEmail) {
            seterror({ ...error, email: true })
        }
        //mobile number validator
        if (e.target.name === "mobile") {
            // console.log(e.target.value)
            if (e.target.value[0] === '0') {
                seterror({ ...error, mobile: true })
                return 0
            } else {
                seterror({ ...error, mobile: false })
            }
            if (!/^[0-9]*$/.test(e.target.value)) {
                return 0
            }
        }
        setUserInput({ ...userInput, [e.target.name]: e.target.value })
    }
    const handleSubmit = e => {
        e.preventDefault()

        if (imgUpLoading) {
            toast.error('please wait, image is uploading to the server')
            return 0
        }
        if (!fileUrls.length) {
            toast.error('please upload Contractor image')
            return 0
        }
        const { sub_estate_manager_id, mobile, email, full_name, address, contractor_type_id } = userInput

        const chkEmail = /\S+@\S+\.\S+/.test(email)
        if (!chkEmail) {
            return 0
        }
        const photo = fileUrls.length ? fileUrls[0].url : null
        const submitData = {
            contractor_id: sub_estate_manager_id,
            contractor_type_id,
            mobile,
            email,
            full_name,
            address,
            photo
        }
        console.log(submitData)
        setLoading(true)
        useJwt.editContractor(submitData).then(res => {
            console.log(res)
            Success(res)
            // history.push(`/${userRole}/MyProperty`)
            setLoading(false)
        }).catch(err => {
            Error(err)
            setLoading(false)
        })
    }
    return (
        <Fragment>
            <Button.Ripple className='ml-1 mb-1' color='primary' onClick={e => history.push(`/${userRole}/ContractorList`)}>
                <ChevronLeft size={12} /> Back
            </Button.Ripple>
            <Card>
                <CardHeader className='border-bottom'>
                    <CardTitle tag='h4'>Update Contractor</CardTitle>
                </CardHeader>
                <CardBody>
                    <Form className="row pt-1" style={{ width: '100%' }} onSubmit={handleSubmit} autoComplete="off">
                        <Col md='4' className="mt-0" >
                            <FormGroup>
                                <Label for="full_name">Fullname</Label>
                                <Input
                                    required
                                    type="text"
                                    id='full_name'
                                    name="full_name"
                                    placeholder="Contractor name"
                                    value={userInput.full_name}
                                    onChange={handleChange}
                                />
                            </FormGroup>
                        </Col>
                        <Col md='4' >
                            <FormGroup>
                                <Label className='form-label' for='mobile'>
                                    Mobile Number
                                </Label>
                                <InputGroup className='input-group-merge w-100 mb-1'>
                                    <InputGroupAddon addonType='prepend'>
                                        <InputGroupText>
                                            {PHONE_PREFIX}
                                        </InputGroupText>
                                    </InputGroupAddon>
                                    <Input
                                        autoFocus
                                        required
                                        type='text'
                                        minLength={10}
                                        maxLength={10}
                                        id='mobile'
                                        name='mobile'
                                        value={userInput.mobile}
                                        onChange={handleChange}
                                        placeholder='123-456-7899'
                                    />
                                </InputGroup>
                                {error.mobile && <span style={{ color: 'red', fontSize: '11px' }}>First digit 0 not allow</span>}
                            </FormGroup>
                        </Col>
                        <Col md='4' >
                            <FormGroup>
                                <Label for="email">Email</Label>
                                <Input
                                    required
                                    type="email"
                                    id='email'
                                    name="email"
                                    placeholder="example@gmail.com"
                                    value={userInput.email}
                                    onChange={handleChange}
                                />
                                {error.email && userInput.email && <span style={{ color: 'red', fontSize: '11px' }}>Please Enter a valid Email</span>}
                            </FormGroup>
                        </Col>
                        <Col md='4' className="mt-0" >
                            <FormGroup>
                                <Label>Select a Contractor Type</Label>
                                <><Select
                                    ref={contractorTypeRef}
                                    theme={selectThemeColors}
                                    className='react-select'
                                    classNamePrefix='select'
                                    name='contractor_type_id'
                                    maxMenuHeight={150}
                                    onChange={selected => {
                                        setUserInput({ ...userInput, contractor_type_id: selected.value, contractor_type_name: selected.label })
                                    }}
                                    options={contractorTypeList.map(x => { return { value: x.id, label: x.type_name } })}
                                    value={{ value: userInput.contractor_type_id, label: userInput.contractor_type_name }}
                                    // isDisabled={!ukaddress.length}
                                    isClearable={false}
                                /><Input
                                        required
                                        style={{
                                            opacity: 0,
                                            width: "100%",
                                            height: 0
                                            // position: "absolute"
                                        }}
                                        onFocus={e => contractorTypeRef.current.select.focus()}
                                        value={userInput.contractor_type_id || ''}
                                        onChange={e => ''}
                                    /></> </FormGroup>
                        </Col>

                        <Col md='4' >
                            <Label for="postcode">Search Address by Post Code</Label>
                            <InputGroup>
                                <Input
                                    // required
                                    type="text"
                                    id='postcode'
                                    name="postcode"
                                    placeholder="post code"
                                    value={postcode}
                                    onChange={e => setPostCode(e.target.value)}
                                />
                                <InputGroupAddon addonType='append'>
                                    <Button color='primary' outline type='button' onClick={handlePostCodeSearch}>
                                        {searching ? <Spinner size='sm' /> : 'Search'}
                                    </Button>
                                </InputGroupAddon>
                            </InputGroup>
                        </Col>
                        <Col md='4' >
                            <Label>Address</Label>
                            <Select
                                ref={addressRef}
                                theme={selectThemeColors}
                                className='react-select'
                                classNamePrefix='select'
                                name='address'
                                onChange={selected => {
                                    setUserInput({ ...userInput, address: selected.value })
                                }}
                                options={ukaddress.map(x => { return { value: x, label: x } })}
                                value={{ value: userInput.address, label: userInput.address }}
                                // isDisabled={!ukaddress.length}
                                isClearable={false}
                            />
                            {/* {ukaddress.length ? <><Select
                                ref={addressRef}
                                theme={selectThemeColors}
                                className='react-select'
                                classNamePrefix='select'
                                name='address'
                                onChange={selected => {
                                    setUserInput({ ...userInput, address: selected.value })
                                }}
                                options={ukaddress.map(x => { return { value: x, label: x } })}
                                value={{ value: userInput.address, label: userInput.address }}
                                // isDisabled={!ukaddress.length}
                                isClearable={false}
                            /><Input
                                    required
                                    style={{
                                        opacity: 0,
                                        width: "100%",
                                        height: 0
                                        // position: "absolute"
                                    }}
                                    onFocus={e => addressRef.current.select.focus()}
                                    value={userInput.address || ''}
                                    onChange={e => ''}
                                /></> : <Input
                                required
                                type="text"
                                id='address'
                                name="address"
                                placeholder="address"
                                value={userInput.address}
                                onChange={handleChange}
                            />} */}
                        </Col>

                        <Col md='12' className='my-0'>
                            <Label>Contractor Image</Label>
                            <ImgUpload
                                setimgUpLoading={setimgUpLoading}
                                state={state}
                                setState={setState}
                                fileUrls={fileUrls}
                                setFileUrls={setFileUrls} />
                        </Col>
                        <Col md="12 text-center">
                            <Button.Ripple className='mt-2' color='primary' type="submit" disabled={isLoading}>
                                {isLoading ? <Spinner size='sm' /> : 'Submit'}
                            </Button.Ripple>
                        </Col>
                    </Form>
                </CardBody>
            </Card>
        </Fragment>
    )
}

export default EditContractor